$backgroundColor:#060a3d;
$color:#fff;
$color1:#868aaf;
$color2:#db3baa;
$color2-dark:darken($color2,10%);
$color2-darker:darken($color2,30%);
$color3:#0025f1;

*{
    margin: 0;
    padding: 0;
}

#player{
    height: 100vh;
    background: $backgroundColor;
    .header{
        height: 100px;
        text-align: center;
        h1{
            color: $color;
            font-size: 20px;
            padding-top: 20px;
        }
        p{
            color: $color1;
            font-size: 12px;
        }
        .balls{
            display: flex;       //居中万金油 1
            justify-content: center; //居中万金油2  水平居中
            align-items:center;//垂直居中
            margin-top: 20px;
            span{
                display: block;
                width: 5px;
                height: 5px;
                background:$color2-darker;
                border-radius: 50%;
                margin: 0 4px;
    
                &.current{
                    width: 8px;
                    height: 8px;
                    background: $color2;
                }
            }
        }
    }
}

.area-effect{
    
    .effect{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60vh;
        >div{
            background-size: contain; //https://blog.csdn.net/caicai1171523597/article/details/105876002
            position: absolute;
        }

        .effect-1{
            background: url("../svg/effect-no-move.svg") 0 0 no-repeat;  //https://blog.csdn.net/x1037490413/article/details/102922939
            width: 70vw;
            height: 70vw;
            animation: rotate1 20s linear infinite; //旋转特效调用  rotate1调用下面写好的旋转设置， 10s完成一次旋转 linear 线性的  infinite无穷的，一直转
        }
        .effect-2{
            background: url("../svg/effect-move1.svg") 0 0 no-repeat;
            width: 60vw;
            height: 60vw;
            animation: rotate1 10s linear infinite reverse ;  //reverse 反过来转
        }
        .effect-3{
            background: url("../svg/effect-move2.svg") 0 0 no-repeat;
            width: 24vw;
            height: 24vw;
            animation: rotate1 10s linear infinite; 
        }
    }

    .lyric{
        text-align: center;
        p{
            font-size: 13px;
            color: $color1;
            margin-top: 10px;
            
            &.current{  //表示既是p标签 也是class为current 相当于是指定p标签
                color:$color;

            }
        }
    }
}


.footer{
    height: 180px;
.buttons{
     display: flex;
     justify-content: center;
     align-items: center;
    

    svg{
        width: 24px;
        height: 24px;
        margin:0 20px;
        margin-top: 20px;
    }
 }

 .actions {
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 20px;

    svg {
      width: 28px;
      height: 28px;

      &.btn-play-pause {
        width: 50px;
        height: 50px;       
      }
    }

  }


 .bar-area {
    color: $color1;
    font-size: 12px;
    display: flex;
    padding: 0 20px;
    margin-top: 20px;
    align-items: center;
    .time-start {
      order: 1;
      width: 40px;
    }
    .time-end {
      order: 3;
      width: 40px;
    }
    .bar {
      order: 2;
      flex: 1;
      height: 4px;
      background: $color3;
      border-radius: 2px;
      margin: 0 10px;
      .progress {
        width: 0%;
        transition: all .2s;
        height: 100%;
        border-radius: 2px;
        background: $color2;
        position: relative;

        &::after {
          content: '';
          position: absolute;
          right: -8px;
          top: -7px;
          display: block;
          width: 16px;
          height: 16px;
          background: url(../svg/progress.svg) 0 0 no-repeat;
          background-size: contain;
        }
      }
    }
  }
}


@keyframes rotate1{  //旋转特效
    0%{
        transform: rotate(0);
    }
    100%{
        transform: rotate(360deg);
    }
}